import { AppBar, Box, CardContent, createMuiTheme, CssBaseline, Dialog, DialogContent, IconButton, makeStyles, ThemeProvider, Toolbar, Typography } from '@material-ui/core'
import deepOrange from '@material-ui/core/colors/deepOrange'
import deepPurple from '@material-ui/core/colors/deepPurple'
import CloseIcon from '@material-ui/icons/Close'
import GitHubIcon from '@material-ui/icons/GitHub'
import MenuIcon from '@material-ui/icons/Menu'
import { AsyncTrunk } from 'mobx-sync'
import React from 'react'
import Helmet, { HelmetProps } from 'react-helmet'

import store, { storeContext } from '../store'
import GridDivider from './GridDivider'
import Menu from './Menu'

const titleTemplate = (t: string) => `${t} | Tools`

const theme = createMuiTheme({
  typography: {
    fontFamily: 'Nunito, sans-serif',
    fontSize: 14,
  },
  palette: {
    primary: {
      main: deepOrange.A400,
      contrastText: '#fff',
    },
    secondary: {
      main: deepPurple[600],
      contrastText: '#fff',
    },
  },
  overrides: {
    MuiCssBaseline: {
      '@global': {
        body: {
          backgroundColor: '#222',
        },
      },
    },
  },
})

interface Props extends HelmetProps {
  disableHeader: boolean
}

const useStyles = makeStyles((theme) => ({
  main: {
    backgroundColor: '#f5f5f5',
    height: '100vh',
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
}))

const Layout = ({ title = 'Home Page', disableHeader = false, children, ...props }: React.PropsWithChildren<Props>) => {
  const { main, menuButton } = useStyles()
  const [open, setOpen] = React.useState(false)

  React.useEffect(() => {
    document.title = titleTemplate(title)
  }, [title])

  React.useEffect(() => {
    const trunk = new AsyncTrunk(store, { storage: localStorage, storageKey: 'store' })
    trunk.init()
  }, [])

  const openGithub = React.useCallback(() => {
    if (typeof window === 'undefined') { return }
    window.open('https://github.com/ericchanky/ericchanky.github.io', '__blank')
  }, [])

  return (
    <storeContext.Provider value={store}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          title={titleTemplate(title)}
          {...props}
        />
        {!disableHeader && (
          <AppBar>
            <Toolbar>
              <IconButton
                color="inherit"
                className={menuButton}
                onClick={() => setOpen(true)}
              >
                <MenuIcon />
              </IconButton>
              <Typography variant="h5" component="h1">{title}</Typography>
              <GridDivider />
              <IconButton
                color="inherit"
                onClick={openGithub}
              >
                <GitHubIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
        )}
        <Box component="main" className={main}>
          {!disableHeader && <Toolbar />}
          {!disableHeader && (
            <CardContent>
              {children}
            </CardContent>
          )}
          {disableHeader && children}
        </Box>
        <Dialog
          fullScreen
          open={open}
          onClose={() => setOpen(false)}
        >
          <AppBar elevation={0} color="inherit" position="relative">
            <Toolbar>
              <GridDivider />
              <IconButton color="inherit" onClick={() => setOpen(false)}>
                <CloseIcon />
              </IconButton>
            </Toolbar>
          </AppBar>
          <DialogContent>
            <Menu />
          </DialogContent>
        </Dialog>
      </ThemeProvider>
    </storeContext.Provider>
  )
}

export default Layout
